.programDocumentDownload {
  &__items {
    margin-bottom: rem-calc(60*2/3);

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
    }

    &__item {
      margin-bottom: rem-calc(20*2/3);

      a {
        display: table;
        width: 100%;
        color: $jet;
      }

      .icon,
      .heading {
        display: table-cell;
        vertical-align: middle;
      }

      .icon {
        width: rem-calc(185*2/3);
        height: rem-calc(185*2/3);
        font-size: rem-calc(80*2/3);
        background-color: $lightGrey;
        text-align: center;
      }

      .heading {
        padding-left: rem-calc(40*2/3);
      }
    }
  }
}

.bg-lightGrey {
  .programDocumentDownload {
    .icon {
      background-color: $white;
    }
  }
}

@media #{$medium-up} {
  .programDocumentDownload {
    @include grid-row();

    &__heading {
      @include grid-column(4);

      .heading {
        padding-top: 0;

        .heading__title {
          font-size: rem-calc(26);
          margin-bottom: rem-calc(20);
        }
      }
    }

    &__items {
      @include grid-column(8);

      .row {
        @include grid-row();

        .programDocumentDownload__items__item {
          @include grid-column(3);

          a {
            display: block;
            width: 100%;
            height: 100%;
          }

          .icon,
          .heading {
            display: block;
          }

          .heading {
            padding-left: 0;
            padding-top: rem-calc(20);
          }

          .icon {
            height: initial;
            width: initial;
            font-size: rem-calc(70);

            &:before {
              display: inline-block;
              vertical-align: middle;
            }

            &:after {
              content: "";
              display: inline-block;
              padding-top: 100%;
              vertical-align: middle;
            }
          }

          .heading__title {
            font-size: rem-calc(16);
          }
        }
      }
    }
  }
}
