.programCharacteristics {
  position: relative;

  &__nav {
    @include position(absolute, null null null 50%);
    @include transform(translateX(-50%));
    margin-top: rem-calc(-20);
    z-index: 1;

    @extend .ul-unstyled;

    li {
      display: inline-block;
      margin-right: rem-calc(10);

      &:last-child {
        margin-right: 0;
      }

      a {
        @include btn-style($blue);
        background-color: $white;
        color: $blue;

        .no-touch &:hover,
        .touch &:active,
        &:focus,
        &.active {
          background-color: $blue;
          color: $white;
        }
      }
    }
  }
}

.programCharacteristicsItem {
  &__img {
    margin-bottom: rem-calc(30*2/3);
    img {
      width: 100%;
    }
  }

  &__content {
    text-align: center;
    .heading--tiny {
      margin-bottom: rem-calc(30*2/3);
    }
  }
}


@media #{$medium-up} {
  .programCharacteristics {
    margin-top: rem-calc(40);

    .slick-prev,
    .slick-next {
      margin-top: -11%;
    }
  }

  .programCharacteristicsItem {
    &__content {
      text-align: left;
      width: 45%;
      margin: 0 auto;
    }
  }
}

@media #{$large-up} {
  .programCharacteristics {
    .slick-prev,
    .slick-next {
      margin-top: -8%;
    }
  }
}