.docs {
  padding: 25px 0;

  @media #{$medium-only} {
    padding: 35px 0;
  }

  &__tabs {
    margin: 0 0 50px;
    text-align: center;

    @media #{$medium-up} {
      margin-bottom: 15px;
    }

    li {
      list-style: none;
      display: inline-block;
      padding: 5px;
    }
  }

  &__tabs__button {
    margin-bottom: 0;
    font-size: rem-calc(22);
    font-weight: bold;
    background-color: transparent;
    border-width: 3px;
    border-color: $lightBlue;
    color: $lightBlue;
    padding: 11px 22px;
    transition-property: background-color, border-color, color;
    transition-duration: 300ms;
    transition-timing-function: ease-out;

    @media #{$medium-up} {
      font-size: rem-calc(18);
      padding: 8px 26px;
    }

    &--active {
      background-color: $blue;
      border-color: $blue;
      color: $white;
    }

    .no-touch &:hover,
    &:active,
    &:focus {
      background-color: $blue;
      border-color: $blue;
      color: $white;
    }
  }

  &__item {
    @media #{$small-only} {
        padding: 2.5% 5%;
    }
    @media #{$medium-only} {
        &:nth-child(3) {
          clear: left;
        }
    }
    @media #{$large-up} {
        &:nth-child(4) {
          clear: left;
        }
    }
    @media #{$medium-up} {
      padding-top: $column-gutter / 2;
      padding-bottom: $column-gutter / 2;
    }
  }

  &__wide__item {
      @media #{$small-only} {
        padding: 0;
      }
  }

  &__wide__img {
      width: 100%;

      @media #{$medium-up} {
        @include clearfix;
        position: relative;
      }
  }

  &__header {
    background-color: $blue;
    margin: -7.5% 5% 0;
    padding: 5%;
    position: relative;
    border-radius: 10px;

    &--grey {
      background-color: $grey;
    }

    @media #{$medium-up} {
      margin: 0;
      padding: 2.5%;
      height: 42.5%;
      width: 46%;
      position: absolute;
      left: 5%;
      bottom: $column-gutter / 2;
    }

    &__wrapper {
      @media #{$medium-up} {
        position: absolute;
        bottom: 7.5%;
        width: 90%;
      }
    }
  }

  &__wide__footer {
    padding: 5%;

    @media #{$medium-up} {
      float: right;
      margin: 0;
      width: 48%;
      padding: 4%;
    }
  }

  &__footer {
    width: 68%;
    float: left;
    padding-left: 5%;

    @media #{$medium-up} {
      width: 100%;
      padding-left: 0;
      padding-top: 5%;
    }
  }

  &__img {
    width: 32%;
    float: left;

    @media #{$medium-up} {
      width: 100%;
    }
  }

  &__type,
  &__date,
  &__title {
    margin-bottom: 0;
    font-weight: bold;
    font-size: rem-calc(30);
    line-height: 110%;

    @media #{$medium-up} {
      font-size: rem-calc(20);
    }
  }

  &__type {
    color: $lightBlue;

    &--grey {
      color: $mediumGrey;
    }
  }

  &__date {
    color: $lightBlue;

    &--grey {
      color: $mediumGrey;
    }
  }

  &__title {
    color: $darkBlue;

    &--header {
      color: $white;
    }
  }

  &__quote p,
  &__readmore {
    font-size: rem-calc(24);
    color: $grey;

    @media #{$medium-up} {
      font-size: rem-calc(16);
    }
  }

  &__quote {
    margin: 0;
    border-left: 0;
    padding: 0 0 5%;

    p {
      margin: 0;
    }
  }

  &__readmore {
    font-weight: bold;

    &:after {
      content: " >";
    }
  }
}