.ourAdvices {

  &__video {
    display: block;
    height: rem-calc(365*2/3);
    background-size: cover;
    background-position: center center;
    text-align: center;
    margin-bottom: rem-calc(55*2/3);

    .icon-play {
      display: block;
      @include position(absolute, 50% null null 50%);
      @include transform(translate(-50%, -50%));
      z-index: 2;
      color: $white;
      font-size: rem-calc(60);
      @include transition-property(all);
      @include transition-duration(.4s);
      @include transition-timing-function(ease-out);
    }

    &:hover {
      .icon-play {
        font-size: rem-calc(70);
      }
    }
  }

  &__text {
    width: 100%;
    margin-bottom: rem-calc(85*2/3);
  }

  &__figure {
    margin-bottom: rem-calc(45*2/3);
  }

  &__heading {
    margin-bottom: rem-calc(40*2/3);
  }


  @media #{$medium-up} {

    .ourAdvices__video {
      max-width: 100%;
      height: rem-calc(380);
      margin-bottom: rem-calc(55);
      margin-top: rem-calc(20);
    }

    .ourAdvices__text {
      width: 66%;
      margin: 0 auto rem-calc(85) auto;
      text-align: center;
    }

    .ourAdvices__figure {
      margin-bottom: rem-calc(45);
    }

    .ourAdvices__heading {
      margin-bottom: rem-calc(40);
    }
  }

}
