// INPUT (Foundation default styles are unconvenient)
#{$all-text-inputs} {
  height: $form-height;
  padding: $form-padding;
  font-size: $input-font-size;
  font-family: $font-family-sans-serif;
  font-weight: $font-weight-medium;
  border: $input-border-width $input-border-style $input-border-color;

  @include placeholder {
    color: $grey;
    font-weight: $font-weight-normal;
  }

  &.small-padding {
    padding-left: rem-calc(5);
    padding-right: rem-calc(5);
  }

  &:focus {
    @include focus();
  }
}

.webform-component {
  label {
    color: $blue;
    font-weight: $font-weight-bold;
    padding: rem-calc(8) 0;
  }

  .form-text,
  .form-textarea {
    border: 2px solid $input-border-color;
  }

  .form-textarea {
    height: rem-calc(150);
  }

  @media #{$medium-up} {
    label {
      padding: rem-calc(15) 0;
    }
  }
}

.error {
  .error,
  .field__input {
    color: $alert-color !important;
  }
  small.error {
    text-transform: none !important;
  }
}


// CHECKBOX
.input-checkbox {
  @extend .visually-hidden;

  &:checked + label:before {
    background: $blue;
    box-shadow: inset 0 0 0 2px $white;
  }
  &:focus + label:before {
    @include focus();
    box-shadow: 0 0 4px 0 $glowing-effect-color,
                inset 0 0 0 2px $white;
  }
}

.label-checkbox {
  display: table;
  line-height: 1;
  padding: rem-calc(15) 0;
  @media #{$medium-up} {
    padding: rem-calc(15) 0;
  }
  cursor: pointer;

  &:before {
    content: "";
    width: rem-calc(30);
    height: rem-calc(30);
    border: 1px solid $blue;
    background: $white;
    display: table-cell;
    vertical-align: middle;

    @media #{$medium-up} {
      width: rem-calc(15);
      height: rem-calc(15);
    }
  }

  span {
    display: table-cell;
    vertical-align: middle;
    padding-left: $form-padding;
  }
}

input[type="checkbox"] + .label-checkbox {
  margin-right: 0;
  margin-left: 0;
}


// AJAX FORM
[data-ajax-form] {
  position: relative;

  [data-ajax-form-response] {
    opacity: 0;
    display: none;
    @include flex-direction(column);
    @include justify-content(center);
    @include position(absolute, 0 0 0 0);
    z-index: 1;
    text-align: center;

    @include transition-property(all);
    @include transition-duration(.4s);
    @include transition-timing-function(ease-out);

    p {
      font-weight: $font-weight-bold;
    }
  }

  [data-ajax-form-content] {
    opacity: 1;
  }
}

.status-ok[data-ajax-form] {
  [data-ajax-form-response] {
    opacity: 1;
    background: $white;
    @include display(flex);

    p {
      color: $success-color;
    }
  }
}

.status-nok[data-ajax-form] {
  [data-ajax-form-response] {
    opacity: 1;
    background: rgba($black, .8);
    @include display(flex);

    p {
      color: $alert-color;
    }
  }
}
