.homeDocs {
  @extend .bg-brown;
  padding: rem-calc(75*2/3) 2.5%;

  @media #{$medium-only} {
    padding: rem-calc(75) $column-gutter;
  }

  @media #{$large-up} {
    padding: rem-calc(75) 0;
  }

  .wrapper {
    @extend .bg-white;
    padding: rem-calc(45) 2.5% rem-calc(15);

    @media #{$medium-up} {
      padding: rem-calc(50) $column-gutter;
    }
  }

  &__main {
    margin-bottom: rem-calc(30*2/3);

    @media #{$medium-up} {
      margin-bottom: 0;
    }

    &__link {
      display: block;
      height: rem-calc(365*2/3);
      background-size: cover;
      background-position: center center;
      @include display(flex);
      @include flex-direction(column);
      @include align-items(center);
      @include justify-content(center);
      text-align: center;
      margin-bottom: rem-calc(30*2/3);

      > * {
        width: 80%;
        color: $white;
      }

      .btn-icon {
        margin-top: rem-calc(20*2/3);
        font-size: rem-calc(18*2/3);
        .icon-play {
          font-size: rem-calc(34*2/3);
        }
      }

      @media #{$medium-up} {
        max-width: 100%;
        height: rem-calc(380);

        .btn-icon {
          font-size: rem-calc(18);
          .icon-play {
            font-size: rem-calc(28);
          }
        }

        .heading__title {
          width: rem-calc(360);
        }
      }
    }

    &__desc {
      border-bottom: 1px solid $mediumGrey;

      p {
        font-weight: $font-weight-bold;
      }

      @media #{$medium-up} {
        position: relative;
        @include grid-row($behavior: nest-collapse);
        max-width: rem-calc(780);
        margin: rem-calc(35) auto 0 auto;
        padding-bottom: rem-calc(35);

        p {
          margin-bottom: 0;
          font-size: rem-calc(18);

          @include grid-column(
            $columns: 8,
            $collapse: true
          );
        }

        p+.btn-wrapper {
          @include position(absolute, null 0 rem-calc(40) null);
          width: rem-calc(175);
          > * {
            width: 100%;
          }
        }

        .btn-wrapper {
          > * {
            margin-bottom: 0;
          }
        }
      }
    }

  }

  &__list {
    margin-left: auto;
    margin-right: auto;

    .fileItem-list {
      padding-bottom: rem-calc(70*2/3);
      margin-bottom: 0;
    }


    @media #{$medium-up} {
      .heading {
        padding: rem-calc(25) 0 rem-calc(20);
      }

      .fileItem-list {
        @include grid-row();
        padding-bottom: 0;
        padding-left: rem-calc(20);
        padding-right: rem-calc(20);

        > * {
          @include grid-column(
            $columns: 3,
            $collapse: true
          );
        }
      }
    }

  }
}

