.iframe {
  position: relative;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  overflow: auto;
  -webkit-overflow-scrolling:touch;

  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  /* proportion value to aspect ratio 16:9 (9 / 16 = 0.5625 or 56.25%) */

  &.iframe--video-de-quartier {
    padding-bottom: 54%;
  }

  &.iframe--visite-virtuelle {
    padding-bottom: 57%;
  }

  &.iframe--vue-generale {
    padding-bottom: 38%;
    max-width: 72%;
    margin: 0 auto;
  }

  &.iframe--plan-3d {
    padding-bottom: 70%;
  }


  @media #{$medium-up} {
    //&.iframe--video-de-quartier {
    //  padding-bottom: 54%;
    //}

    &.iframe--visite-virtuelle {
      padding-bottom: 38%;
    }

    //&.iframe--vue-generale {
    //  padding-bottom: 38%;
    //  max-width: 72%;
    //  margin: 0 auto;
    //}

    //&.iframe--plan-3d {
    //  padding-bottom: 38%;
    //}
  }
}