// BUTTONS
@mixin btn-style($background:$blue, $color:$white) {
  @include button(
    $bg: $background
  );
  padding: rem-calc(16*2/3) rem-calc(20*2/3);
  border: 1px solid $background;
  color: $color;
  font-size: rem-calc(24*2/3);
  line-height: 1;
  vertical-align: middle;

  .no-touch &:hover,
  .touch &:active,
  &:focus {
    background-color: $color;
    color: $background;
  }

  &:focus {
    @include focus();
  }

  .icon-arrow,
  .icon-expand {
    margin-left: rem-calc(10);
    vertical-align: inherit;
    &:before {
      display: inline-block;
    }
  }

  @media #{$medium-up} {
    padding: rem-calc(18) rem-calc(24);
    font-size: rem-calc(16);
  }
}


// BTN TYPE
.btn-primary {
  @include btn-style($blue, $white);
}

.btn-secondary {
  @include btn-style($jet, $white);
}

.btn-white {
  @include btn-style($white, $mediumGrey);
  @extend .btn-rounded;
  border-color: $mediumGrey;
  color: $jet;
  font-weight: $font-weight-normal;

  .icon {
    color: $blue;
  }

  .no-touch &:hover,
  .touch &:active,
  &:focus {
    color: $jet;
  }
}


// FILTERS
.active {
  .btn-filter {
    border-color: $blue;
    color: $blue;
  }
}
.btn-filter {
  background: $white;
  font-size: $button-font-med;
  font-weight: $font-weight-bold;
  border: solid 3px $lightBlue;
  color: $lightBlue;
  padding: $button-sml ($button-lrg * 2);
  display: inline-block;
  transition-property: border-color, color;
  transition-duration: 300ms;
  transition-timing-function: ease-out;

  .no-touch &:hover,
  &:active,
  &.active {
    border-color: $blue;
    color: $blue;
  }

  &:focus {
    @include focus();
  }

  @media #{$medium-up} {
    font-size: $button-font-lrg;
    padding: rem-calc(10) rem-calc(20);
  }
}


// BUTTON WITH ICON
.btn-icon {
  position: relative;
  .button__content {
    display: inline-block;
    position: relative;
    padding-left: rem-calc(35);
  }

  .icon {
    vertical-align: middle;
    @include position(absolute, 50% null null 0);
    @include transform(translateY(-50%));
    font-size: 150%;
  }
}


// PHONE
.btn-phone {
  @include btn-style($white, $blue);
  border: 1px solid $blue;
  box-shadow: inset 0 0 0 2px $blue;
}
.btn-phone-green {
  @include btn-style($white, $green);
  border: 1px solid $green;
  box-shadow: inset 0 0 0 2px $green;
  span {
    opacity: .5;
  }
}
.btn-phone,
.btn-phone-green {
  background: transparent;
  border-radius: 4px;
}
@media #{$medium-up} {
  .btn-phone,
  .btn-phone-green {
    font-size: rem-calc(16);
    padding-top: rem-calc(13);
    padding-bottom: rem-calc(14);
  }
}


// ROUNDED BUTTONS
.btn-rounded {
  border-radius: 4px;
  padding: rem-calc(30*2/3);
  font-size: rem-calc(24*2/3);

  @media #{$medium-up} {
    padding: rem-calc(15) rem-calc(20);
    font-size: rem-calc(13);
  }
}


// UNSTYLE BUTTON
.btn-unstyled {
  cursor: pointer;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  background: transparent;
  padding: inherit;
  text-align: inherit;
  border: none;
  display: inline-block;
  @include appearance(none);
  font-family: $font-family-sans-serif;

  .no-touch &:hover,
  &:active {
    background: inherit;
    color: inherit;
  }

  &:focus {
    @include focus();
  }
}


// btn wrapper
.btn-wrapper {
  text-align: center;

  &.btn-wrapper--center {
    @media #{$medium-up} {
      text-align: center;
    }

    > * {
      @media #{$small-only} {
        width: 100%;
      }
    }
  }
}