// SELECT-LIKE DROPDOWN
.form-dropdown {
  background: $input-bg-color;
  border: $input-border-width $input-border-style $input-border-color;
  border-radius: $input-border-radius;
  color: $input-font-color;
  margin-bottom: $form-spacing;
  min-height: $form-height;
  position: relative;

  &__trigger {
    @extend .btn-unstyled;
    position: relative;
    width: 100%;
    height: $form-height;
    padding: $form-padding rem-calc(60) $form-padding $form-padding;
    font-weight: $form-label-font-weight;
    font-size: $input-font-size;

    &:focus {
      @include focus();
    }

    .icon-expand {
      @include position(absolute, 50% 0 null null);
      @include transform(translateY(-50%));
      @include transition-property(all);
      @include transition-duration(.4s);
      @include transition-timing-function(ease-out);
      color: $blue;
      font-size: ($input-font-size * 1.35);
      margin-right: $form-padding;
    }

    &[aria-expanded="true"] {
      .icon-expand {
        @include transform(rotate(180deg) translateY(50%));
      }
    }
  }

  &__content {
    padding: $form-padding $form-padding 0 $form-padding;
    background: $input-bg-color;

    .undo-padding {
      margin-top: (-$form-padding);
    }

    position: absolute;
    z-index: 1;
    background: $input-bg-color;
    border: $input-border-width $input-border-style $input-border-color;
    width: rem-calc(250);
    box-shadow: rem-calc(3) rem-calc(3) rem-calc(3) 0 rgba($black, .2);

    .bordered {
      position: relative;

      &:after {
        content: "";
        //@include position(absolute, null (-$form-padding) 0 (-$form-padding));
        @include position(absolute, null 0 0 0);
        height: 1px;
        background-color: $input-border-color;
      }
    }

    .label-checkbox {
      display: block;
    }
  }
}


// "?" picto
.infotip {
  position: relative;
  span { @extend .visually-hidden; }

  &:after {
    content: "?";
    cursor: pointer;
    display: inline-block;
    width: rem-calc(20);
    height: rem-calc(20);
    border-radius: 50%;
    background: $blue;
    color: $white;
    @include position(absolute, 0 0 0 0);
    line-height: 1.3;
    text-align: center;
    font-size: rem-calc(15);
  }
}
_:-ms-lang(x), .infotip:after { line-height: 1.4; } // IE10 and IE11 hack


// Button dropdown
[data-dropdown] {
  .icon-expand {
    @include transition-property(all);
    @include transition-duration(.4s);
    @include transition-timing-function(ease-out);
    &:after {
      @include position(absolute, 50% 0 null null);
    }
  }

  &[aria-expanded="true"] {
    .icon-expand {
      @include transform(rotate(180deg));
    }
  }
}

.f-dropdown {
  padding: rem-calc(10);
  opacity: 0;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, .5);
  @include transform(translateY(-10px));
  @include transition-property(opacity, transform);
  @include transition-duration(.3s);
  @include transition-timing-function(ease-out);

  &.f-open-dropdown {
    opacity: 1;
    @include transform(translateY(0));
    outline: none;
  }
}


// ajax wait
@include keyframes(rotation) {
  from {
    @include transform(rotate(0deg));
  }
  to {
    @include transform(rotate(359deg));
  }
}
.ajax-wait {
  position: relative;
  background: $grey;
  color: $grey;
  border: $grey;

  .no-touch &:hover,
  .touch &:active,
  &:focus {
    background-color: $grey !important;
    color: $grey !important;
    border: $grey !important;
    box-shadow: 0 0 4px 0 $grey;
    border-color: $grey !important;
    outline: none;
  }

  &:after {
    content: '';
    @include position(absolute, 0 0 0 0);
    background: rgba($grey,.5);
    z-index: 1;
  }

  &:before {
    content: '\EA03';
    font-family: 'icons';
    @include position(absolute, 0 0 0 0);
    z-index: 2;
    color: $white;
    @include flex-direction(column);
    @include justify-content(center);
    @include display(flex);

    @include animation(rotation 1s ease-in infinite);
  }

}


// burger button
.menu-btn {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: $topbar-height;
  height: $topbar-height;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
  background: $white;

  &:focus {
    outline: none;
  }

  span {
    display: block;
    @include position(absolute, 50% rem-calc(20) null rem-calc(20));
    @include transform(translateY(rem-calc(-3)));
    height: rem-calc(3);
    background: $black;
    transition: background 0s 0.2s;

    &:before,
    &:after {
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: rem-calc(3);
      background-color: $black;
      content: "";
      transition-duration: 0.2s, 0.2s;
      transition-delay: 0.2s, 0s;
    }

    &:before {
      top: -10px;
      transition-property: top, transform;
    }

    &:after {
      bottom: -10px;
      transition-property: bottom, transform;
    }
  }

  &.is-active span {
    background-color: $white;

    &:before {
      top: 0;
      transform: rotate(45deg);
    }

    &:after {
      bottom: 0;
      transform: rotate(-45deg);
    }

    &:before,
    &:after {
      transition-delay: 0s, 0.3s;
    }
  }
}