.programParcel {
  &.slick-slider {
    padding-bottom: 0;

    .slick-dots {
      bottom: rem-calc(-35);
    }
  }

  .slick-slide {
    padding-left: $column-gutter;
    padding-right: $column-gutter;
  }
}

.programParcelItem {
  &__heading {
    font-size: rem-calc(24*2/3);
    font-weight: $font-weight-bold;
  }

  &__content {
    padding: rem-calc(20*2/3) rem-calc(40*2/3);
    background: $white;
  }

  &__prices {
    font-size: rem-calc(30*2/3);
    font-weight: $font-weight-bold;
    p {
      margin-bottom: 0;
      font-weight: $font-weight-bold;
      line-height: 2;
      > span {
        vertical-align: middle;
      }
      .tva {
        margin-left: rem-calc(25*2/3);
      }
    }
  }
}

@media #{$medium-up} {
  .programParcel {
    &.slick-slider {
      margin-left: auto;
      margin-right: auto;
    }

    .slick-slide {
      @include display(flex);
      @include flex-wrap(wrap);
      padding-left: ($column-gutter * 3);
      padding-right: ($column-gutter * 3);
      > * {
        width: 33.3333%;
      }
    }
  }

  .programParcelItem {
    padding: rem-calc(10);

    &__heading {
      font-size: rem-calc(16);
    }

    &__prices {
      font-size: rem-calc(20);
    }
  }
}

@media #{$medium-only} {
  .programParcel {
    .slick-prev,
    .slick-next {
      width: rem-calc(40);
      height: rem-calc(40);
      padding: 0;

      .icon {
        @include position(absolute, rem-calc(12) rem-calc(12) rem-calc(12) rem-calc(12));
      }
    }

    .slick-slide {
      padding-left: rem-calc(50);
      padding-right: rem-calc(50);
    }
  }
}

@media #{$large-up} {
  .programParcel {
    .slick-slide {
      padding-left: rem-calc(90);
      padding-right: rem-calc(90);
    }
  }
}