.articleList {
  .heading {
    padding: 0;
  }
  .heading__title--sub {
    color: $jet;
  }
  &__item {
    &__infos {
      padding: 0 $column-gutter rem-calc(60*2/3);
      margin: rem-calc(-3) 0 1px;
      border: 1px solid $mediumGrey;
      border-top: 0;
      background-color: $white;
      &__heading {
        padding-top: rem-calc(35*2/3);
        font-size: rem-calc(36*2/3);
        font-weight: $font-weight-bold;
        text-align: center;
      }
      &__links  {
        color: $grey;
        margin-bottom: 0;
        li {
          color: $grey;
        }
        a {
          color: $grey;
        }
      }
    }
  }
}
@media #{$medium-up} {
  .articleList {
    .heading {
      padding-bottom: rem-calc(35);
    }
    &__list{
      overflow: hidden;
      margin: 0 -$column-gutter/2;
    }
    &__item {
      float: left;
      width: 33.33333%;
      padding: 0 $column-gutter/2;
      &__infos {
        padding-bottom: rem-calc(50);
        &__heading {
          font-size: rem-calc(20);
          min-height: rem-calc(75);
          line-height: rem-calc(24);
          text-align: left;
        }
      }
    }
  }
}
