.fileItem {
  text-align: center;

  &__img {
    position: relative;
    display: block;
    img {
      min-width: 100%;
      display: block;
    }

    .icon-play {
      display: block;
      @include position(absolute, 50% null null 50%);
      @include transform(translate(-50%, -50%));
      z-index: 2;
      color: $white;
      font-size: rem-calc(60);
      @include transition-property(all);
      @include transition-duration(.4s);
      @include transition-timing-function(ease-out);
    }

    &:hover {
      .icon-play {
        font-size: rem-calc(70);
      }
    }
  }

  &__infos {
    padding: rem-calc(35*2/3) rem-calc(20*2/3) rem-calc(135*2/3);
    position: relative;
    border: 1px solid $mediumGrey;
    border-top: none;
    background: $white;

    .btn-wrapper {
      @include position(absolute, null 0 rem-calc(20) 0);

      > * {
        margin-bottom: 0;
      }
    }

    @media #{$medium-up} {
      padding: rem-calc(15) rem-calc(20) rem-calc(70);
    }

    &__heading {
      @media #{$medium-up} {
        min-height: rem-calc(60);
      }
      margin-top: 0;
      font-weight: $font-weight-bold;
      font-size: rem-calc(30*2/3);
    }
  }
}

@media #{$medium-up} {
  .fileItem + .fileItem {
    .fileItem__infos {
      border: 1px solid $mediumGrey;
      border-top: none;
      border-left: none;
    }
  }
}
