.close-reveal-modal {
  border-radius: 50%;
  background: $blue;
  border: 1px solid $blue;
  padding: 0;

  @include transition-property(all);
  @include transition-duration(.6s);
  @include transition-timing-function(ease-out);

  &:before {
    color: $white;
  }

  .no-touch &:hover,
  .touch &:active {
    background: $white;
    @include transform(rotate(179.5deg));

    &:before {
      color: $blue;
    }
  }
}

// IE10 and IE11 hacks for a stupid CSS behavior
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .close-reveal-modal {
    &:before {
      padding: 1px 1px 0 1px;
    }
  }
}

@media #{$medium-up} {
  .reveal-modal {
    padding: $reveal-modal-padding-medium;
  }
}


// customize reveal CSS
.reveal-modal-bg {
  display: none !important;
}

.no-scroll {
  overflow: hidden;
}

.reveal-modal {
  overflow: scroll;
  -webkit-overflow-scrolling: touch;

  position: fixed;
  top: 0!important;
  height: 100vh;
  background: rgba($black, .5);
  padding: 0 0 rem-calc(50) 0;
  font-size: 0;

  &:before {
    content: "";
    height: 100%;
    display: inline-block;
    vertical-align: middle;
  }

  &__wrapper {
    opacity: 1 !important;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    background: $white;
    width: 100%;
    padding: rem-calc(60) rem-calc(30) rem-calc(30);
  }

  &__close {
    position: absolute;
    z-index: 1;
    padding: rem-calc(30);
    width: 100%;
  }

  > * {
    opacity: 0;
  }
}

@media #{$medium-up} {

  .reveal-modal {
    padding: rem-calc(30) 0;

    &__wrapper,
    &__close {
      width: 80%;
      margin-left: 10%;
    }
  }

}
