/*! Pushy - v0.9.2 - 2014-9-13
* Pushy is a responsive off-canvas navigation menu using CSS transforms & transitions.
* https://github.com/christophery/pushy/
* by Christopher Yee */

/* Menu Appearance */
.pushy{
  position: fixed;
  width: rem-calc(250);
  height: 100%;
  top: 0;
  z-index: 9999;
  background: $off-canvas-bg;
  overflow: auto;
  //-webkit-overflow-scrolling: touch; /* enables momentum scrolling in iOS overflow elements */

  &__menu {
    margin: 0;
    opacity: 0;

    &__item {
      a{
        display: block;
        color: $white;
        height: $topbar-height;
        line-height: $topbar-height;
        padding-left: rem-calc(20);
        padding-right: rem-calc(20);
        font-size: rem-calc(25*2/3);
        border-bottom: 1px solid rgba($lightBlue, .5);
        text-decoration: none;

        &:active,
        &:hover {
          background: $blue;
        }
      }

      &:last-child {
        a {
          border-bottom: none;
        }
      }
    }
  }
}

/* Menu Movement */
.pushy-left {
  @include transform(translate3d(rem-calc(-250), 0, 0));
}

.pushy-open {
  @include transform(translate3d(0, 0, 0));

  .pushy__menu {
    opacity: 1;
  }
}

.container-push,
.push-push,
.pushy-active .header {
  @include transform(translate3d(rem-calc(250), 0, 0));
}

/* Menu Transitions */
.pushy__menu {
  @include transition-property(opacity);
  @include transition-duration(.1s);
  @include transition-timing-function(cubic-bezier(.16, .68, .43, .99));
}

.pushy,
#container,
.push,
.header {
  @include transition-property(transform);
  @include transition-duration(.4s);
  @include transition-timing-function(cubic-bezier(.16, .68, .43, .99));
}


/* Site Overlay */
.site-overlay {
  display: none;
}

.pushy-active {
  .site-overlay {
    display: block;
    @include position(fixed, 0 0 0 0);
    z-index: 9998;
    cursor: $off-canvas-overlay-cursor;
    //background-color: $off-canvas-overlay-background;
    //@include animation(fade .4s ease-in);
  }
}

