.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  @include user-select(none);
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
  padding-bottom: rem-calc(50*2/3);
  margin-bottom: rem-calc(20);

  @media #{$medium-up} {
    padding-bottom: 0;
    margin-left: rem-calc(-10);
    margin-right: rem-calc(-10);

    &.fullwidth {
      margin-left: 0;
      margin-right: 0;
    }
  }

  &__item {
    &:focus {
      @include focus();
    }
  }

  .slick-prev,
  .slick-next {
    @include position(absolute, 50% null null null);
    @include transform( translateY(-50%) );
    z-index: 1;
    margin: 0;
    width: rem-calc(60);
    height: rem-calc(60);
    @extend .btn-secondary;
    @extend .btn-rounded;

    .icon {
      @include position(absolute, rem-calc(20) rem-calc(20) rem-calc(20) rem-calc(20));
      display: block;
      font-weight: normal;
      border-top: 1px solid $white;
      border-right: 1px solid $white;
    }

    .no-touch  &:hover .icon,
    &:focus .icon {
      border-color: $jet;
    }
  }

  .slick-prev {
    left: 0;
    .icon {
      @include transform( translateX(rem-calc(5)) rotate(-135deg) );
    }
  }

  .slick-next {
    right: 0;
    .icon {
      @include transform( translateX(rem-calc(-5)) rotate(45deg) );
    }
  }
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}

.slick-slider {
  .slick-track,
  .slick-list {
    @include transform(translate3d(0, 0, 0));
  }
}


.slick-track {
  @include position(relative, 0 0 null null);
  display: block;

  &:before,
  &:after {
    content: "";
    display: table;
  }

  &:after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  [dir="rtl"] & {
    float: right;
  }
  img {
    display: block;
    width: 100%;
  }
  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }

  &:focus {
    outline: none;
  }
}

.slick-arrow.slick-hidden {
  display: none;
}


// DOTS

.slick-dots {
  @include position(absolute, null 0 rem-calc(12*2/3) 0);
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;

  li {
    display: inline-block;
    width: rem-calc(12*2/3);
    height: rem-calc(12*2/3);
    margin: 0 rem-calc(10*2/3);
    padding: 0;
    cursor: pointer;

    button {
      position: relative;
      font-size: 0;
      line-height: 0;
      display: block;
      cursor: pointer;
      color: transparent;
      border: 0;
      padding: 0;
      margin: 0;
      width: 100%;
      height: 100%;
      outline: none;
      background: transparent;

      &:before {
        content: "";
        @include position(absolute, 0 0 0 0);
        background: $mediumGrey;
        border-radius: 50%;
      }

      .no-touch &:hover,
      &:active,
      &:focus {
        outline: none;

        &:before {
          background: $grey;
        }
      }
    }
  }
}

.slick-dots li.slick-active button:before {
  background: $blue;
}


@media #{$medium-up} {
  .slick-slider__item {
    margin: 0 rem-calc(10);
  }

  .slick-slider__item-4 {
    .heading__title {
      font-size: rem-calc(18);
    }

    .heading__title--sub {
      font-size: rem-calc(18);
    }
  }
}


// add space between slides on small devices
@media #{$small-only} {
  .slick-slide {
    margin: 0 $column-gutter;
  }

  .slick-slider {
    margin: 0 (-$column-gutter);
  }
}
