.adviceMoreArticle {
  .heading {
    padding: 0;
  }
  .heading__title--sub {
    color: $lightBlue;
  }
  &__item {
    text-align: center;
    &__infos {
      position: relative;
      padding: 0 $column-gutter/2 rem-calc(165px*2/3);
      margin-top: rem-calc(-3px);
      border: 1px solid $mediumGrey;
      border-top: 0;
      &__heading {
        padding-top: rem-calc(35px*2/3);
        font-size: rem-calc(36px*2/3);
      }
      p {
        margin: 0;
        font-size: rem-calc(24px*2/3);
      }
      .btn-wrapper {
        position: absolute;
        left: 0;
        right: 0;
        bottom: rem-calc(60px*2/3);
      }
      .btn-primary {
        margin: 0;
        min-width: percentage(395/560);
      }
    }
  }
}
@media #{$medium-up} {
  .adviceMoreArticle {
    .heading {
      padding-bottom: rem-calc(40px);
    }
    &__list{
      overflow: hidden;
      margin: 0 -$column-gutter/2;
    }
    &__item {
      float: left;
      width: 33.33333%;
      padding: 0 $column-gutter/2;
      &__infos {
        padding-bottom: rem-calc(98px);
        &__heading {
          min-height: rem-calc(85px);
          padding-top: rem-calc(27px);
          font-size: rem-calc(20px);
          line-height: rem-calc(24px);
          font-weight: bold;
        }
        p {
          font-size: rem-calc(14px);
        }
        .btn-wrapper {
          bottom: rem-calc(34px);
        }
      }
    }
  }
}
