.title-area {
  position: relative;
  float: left;
  margin: 0;
  height: 100%;

  .name {
    display: table;
    width: rem-calc(300);
    height: 100%;
    padding-right: rem-calc(15);

    .title-area__link {
      display: table-cell;
      vertical-align: middle;

      img {
        vertical-align: initial;
      }
    }
  }

  .has-form {
    @include position(absolute, 0 -240px null null);
    min-width: 14rem;
  }
}


.sticky {
  &.fixed {
    box-shadow: 0 0 6px 0 rgba($black, .5);
  }
}


.header {
  margin: 0 auto;
  margin-bottom: 0;
  max-width: 75rem;
  background: $white;
  overflow: visible;
  height: $topbar-height;
  line-height: $topbar-height;
  position: relative;

  &__menu,
  &__btn {
    display: inline-block;
    vertical-align: middle;
  }

  &__menu {
    position: relative;
    width: auto;
    float: right;

    .main-menu {
      display: inline;
      height: auto !important;
      width: auto;
      vertical-align: middle;

      &__item {
        float: left;
        list-style: none;

        a {
          color: $topbar-link-color;
          line-height: $topbar-height;
          padding: 0 $topbar-link-padding;
          font-size: $topbar-link-font-size;
          font-weight: $topbar-link-weight;
          display: block;
          text-transform: none;
          width: 100%;

          &:hover {
            color: $topbar-link-color-hover;
          }
        }
      }
    }

  }

  &__btn {
    margin-bottom: 0;
  }
}


