.contactUs {

  &__informations {
    background: $lightGrey;
    padding: $column-gutter-small rem-calc(35*2/3);
    margin-bottom: $column-gutter-small;
    text-align: center;

    &__heading {
      color: $jet;
      font-size: rem-calc(26*2/3);
      font-weight: $font-weight-bold;
      margin-bottom: rem-calc(6*2/3);
    }

    &__text {
      color: $blue;
      font-size: rem-calc(22*2/3);
      font-weight: $font-weight-bold;
      margin-bottom: rem-calc(20*2/3);
    }
  }

  &__cta,
  &__informations__cta {
    > a {
      display: block;
      width: 100%;
      margin-bottom: rem-calc(20*2/3);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

}

@media #{$medium-up} {
  .contactUs {
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    @include display(flex);
    @include align-items(stretch);

    &__img,
    .wrapper {
      padding: 0;
      background: $lightGrey;
    }

    .wrapper {
      @include display(flex);
      @include align-items(center);
      @include flex-direction(column);
      @include justify-content(space-between);
    }

    &__img {
      width: 30%;
      background-size: cover;
    }

    &__informations {
      text-align: left;
      margin-bottom: 0;
      padding: rem-calc(30) rem-calc(35);
      max-width: 100%;

      &__heading {
        font-size: rem-calc(26);
        font-weight: $font-weight-bold;
        line-height: 1.2;
        margin-bottom: rem-calc(6*2/3);
      }

      &__text {
        font-size: rem-calc(18);
        font-weight: $font-weight-bold;
        margin-bottom: rem-calc(20*2/3);
        line-height: 1.2;
      }

      &__cta {
        > a {
          display: inline-block;
          vertical-align: top;
          width: auto;
          margin-right: rem-calc(20);
          border-radius: 5px;
        }
      }
    }

    &__cta {
      width: 100%;
      overflow: hidden;

      > a {
        float: left;
        width: 50%;
        border-radius: 0;
        font-size: rem-calc(26);
        margin-bottom: 0;
      }
    }

  }
}

@media #{$large-up} {
  .contactUs {
    width: 71%;

    &__informations {
      &__text {
        font-size: rem-calc(20);
      }
    }
  }
}