.editorialContentArticle {
  * {
    line-height: 1.3;
  }

  p,
  .editorialContentArticle__content__hightlight,
  ul li,
  ol li,
  .editorialContentArticle__figure {
    margin-bottom: rem-calc(30*2/3);
    font-size: rem-calc(24*2/3);
  }

  sub,
  sup {
    font-size: 70%;
  }
  sub {
    bottom: - rem-calc(0.3);
  }
  sup {
    top: - rem-calc(0.6);
  }

  h1, h2, h3, h4, h5, h6 {
    margin-bottom: rem-calc(45*2/3);
  }
  h1 {
    font-size: rem-calc(36*2/3 + 10);
  }
  h2 {
    font-size: rem-calc(36*2/3 + 8);
  }
  h3 {
    font-size: rem-calc(36*2/3 + 6);
  }
  h4 {
    font-size: rem-calc(36*2/3 + 4);
  }
  h5 {
    font-size: rem-calc(36*2/3 + 2);
  }
  h6 {
    font-size: rem-calc(36*2/3);
  }

  .editorialContentArticle__content__hightlight {
    font-weight: $font-weight-bold;
    color: $grey;
  }

  blockquote {
    border: 0;
    padding: 0;
    margin: rem-calc(90*2/3) 0 rem-calc(45*2/3);
    position: relative;
    &:before {
      content: '';
      font-size: rem-calc(126*2/3);
      font-weight: $font-weight-bold;
      line-height: 1;
      color: $blue;
      position: absolute;
      top: - rem-calc(60*2/3);
      left: 0;
      opacity: .2;
    }
    p {
      color: $blue;
      font-size: rem-calc(36*2/3);
      font-weight: $font-weight-thin;
    }
  }
}

@media #{$medium-up} {
  .editorialContentArticle {
    * {
      line-height: 1.5;
    }

    p,
    .editorialContentArticle__content__hightlight,
    ul li,
    ol li,
    .editorialContentArticle__figure {
      margin-bottom: rem-calc(30);
    }

    &__figure {
      width: 49%;
      float: right;
    }

    &__content {
      width: 48%;
      float: left;
    }

    p {
      font-size: rem-calc(14);
    }

    .editorialContentArticle__content__hightlight {
      font-size: rem-calc(20);
      margin-bottom: rem-calc(50);
    }

    blockquote {
      margin: rem-calc(80) 0 rem-calc(60);
      &:before {
        font-size: rem-calc(80);
      }
      p {
        font-size: rem-calc(30);
      }
    }
  }
}
