html,
body {
  text-rendering: geometricPrecision;
}

a {
  &:focus {
    @include focus();
  }
}

p {
  color: $grey;
  font-size: rem-calc(16);
  letter-spacing: 0.04em;
  line-height: 1.4;

  @media #{$medium-up} {
    line-height: 1.6;
  }
}

sup {
  font-size: 0.7em;
}

.text-bold {
  font-weight: $font-weight-bold;
}

.text-normal {
  font-weight: $font-weight-normal;
}


/* -------- */
/* HEADINGS */
/* -------- */

.heading {
  position: relative;
  padding: rem-calc(18) 0;

  &__title {
    font-weight: $font-weight-bold;
    line-height: 130%;
    font-size: rem-calc(40*2/3);
    margin: 0;
    color: $jet;
    display: block;
  }

  &__title--sub {
    color: $blue;
    margin-bottom: rem-calc(10);
    font-size: rem-calc(40*2/3);
  }

  &__text {
    font-weight: $font-weight-bold;
    font-size: rem-calc(20);
  }

  @media #{$medium-up} {
    padding: rem-calc(40) 0 rem-calc(10);

    &__title {
      font-size: rem-calc(38);
      &--sub {
        font-size: rem-calc(26);
      }
    }
  }
}

.heading--bordered {
  &:before {
    content: "";
    @include position(absolute, 0 null null 0);
    width: rem-calc(40);
    height: rem-calc(5);
    background: $jet;
  }

  @media #{$medium-up} {
    padding: rem-calc(24) 0 rem-calc(20);

    &:before {
      content: "";
      @include position(absolute, 0 null null 0);
      width: rem-calc(50);
      height: rem-calc(7);
      background: $jet;
    }
  }
}

.heading--small {
  .heading__title {
    font-size: rem-calc(28*2/3);
    @media #{$medium-up} {
      font-size: rem-calc(24);
    }
  }

  .heading__title--sub {
    font-size: rem-calc(28*2/3);
    @media #{$medium-up} {
      font-size: rem-calc(20);
    }
  }
}


.heading--white {
  max-width: rem-calc(700);

  &:before {
    background: $white;
  }

  .heading__title {
    color: $white;
    line-height: 130%;
    font-size: rem-calc(36*2/3);
  }

  .heading__title--sub {
    font-size: rem-calc(36*2/3);
    color: rgba($white, .5);
  }

  .heading__title--sub strong {
    color: $white;
  }

  @media #{$medium-up} {
    .heading__title {
      font-size: rem-calc(38);
    }

    .heading__title--sub {
      font-size: rem-calc(38);
    }
  }
}

.heading--tiny {
  text-align: center;
  color: $jet;
  font-size: rem-calc(30*2/3);
  font-weight: $font-weight-bold;
  margin-bottom: rem-calc(45*2/3);

  @media #{$medium-up} {
    font-size: rem-calc(26);
  }
}

.heading--large {
  .heading__title {
    font-size: rem-calc(54*2/3);
    font-weight: $font-weight-bold;
    margin-bottom: rem-calc(30*2/3);
  }
  @media #{$medium-up} {
    .heading__title {
      font-size: rem-calc(50);
      margin-bottom: 0;
    }
  }
}
