/**
 * Variables
 */

$color-grey: $input-border-color;
$base-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
$color-border-grey: $input-border-color;
$base-radius: 0;
$color-hover: $blue;

/**
 * Wrapper
 */
.combo-select{
  position: relative;
  width: 100%;

  input[type=text] {
    height: $form-height;
    padding: $form-padding;
    font-size: $input-font-size;
    font-family: $font-family-sans-serif;
    font-weight: $font-weight-medium;
    border: 2px $input-border-style $input-border-color;

    &[readonly] {
      background: $white;
    }
  }
}

/**
 * Input field
 */
.combo-input{
  @include appearance(none);
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;

  &:focus{
    outline: none;
  }
}

/**
 * Arrow
 */
.combo-select-arrow{
  @include position(absolute, $form-padding $form-padding null null);
  width: rem-calc(16);
  height: rem-calc(16);
  cursor: pointer;
  text-align: center;
  color: $blue;
  display: inline-block;
  font-family: 'icons';
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  text-transform: none;
  font-size: rem-calc(16);
  @extend .icon-expand;

  @include transition-property(all);
  @include transition-duration(.4s);
  @include transition-timing-function(ease-out);

  &:before {
    @include position(absolute, 0 0 0 0);
  }
}

.combo-open{
  .combo-select-arrow{
    @include transform(rotate(180deg));
  }
}

/**
 * When focused
 */
.combo-focus{
  input {
    @include focus();
  }
}

/**
 * Hide native select
 */

.combo-select select{
  @include position(absolute, 0 null null 0);
  z-index: 1;
  width: 100%;
  height: 100%;
  @include appearance(none);
  opacity: 0;
}

@media #{$medium-up} {
  .combo-select select{
    left: -1px;
    top: -1px;
    width: 0;
    height: 0;
    margin: 0;
  }
}

/**
 * Selected option
 */

.option-selected{
  background-color: #eee;
}

/**
 * Hovered option
 */

.option-hover{
  background-color: $color-hover;
  color: #fff;
}

/**
 * Option item
 */

.option-item{
  cursor: pointer;
  border-bottom: 1px solid $color-border-grey;

  &:hover{
    background-color: $color-hover;
    color: $white;
  }

  &:last-child{
    border-bottom: none;
  }
}

/**
 * Disabled and optgroups
 */

.option-group{
  cursor: text;
  font-weight: 600;
  background: #e1e1e1;
  border: 1px #ccc solid;
  border-width: 1px 0;
}

/**
 * Disabled
 */

.option-disabled{
  opacity: 0.5;
}


/**
 * Dropdown
 */

.combo-dropdown{
  @include position(absolute, 100% null null 0);
  z-index: 1;
  width: 100%;
  margin: 0;
  padding: 0;
  display: none;
  overflow-y: auto;
  background: $white;
  border: 1px solid $color-grey;
  padding: 0 $form-padding;

  font-size: rem-calc(14);

  opacity: 0;
  @include transform(translateY(-10px));
  @include transition-property(opacity, transform);
  @include transition-duration(.3s);
  @include transition-timing-function(ease-out);

  li{
    list-style: none;
    padding: 8px 1em;
    margin: 0;
  }
}

/**
 * On Active
 */
.combo-open .combo-dropdown{
  display: block;
  opacity: 1;
  @include transform(translateY(0));
}

/**
 * Search marker
 */
.combo-marker{
  text-decoration: underline;
}