.styleguide {
  h2 {
    padding-top: 50px;
    border-bottom: 2px solid $jet;
  }
}


.colors {
  @include block-grid(
    $per-row: 2,
    $spacing: $block-grid-default-spacing,
    $base-style: true
  );

  @media #{$medium-up} {
     @include block-grid(
      $per-row: 5,
      $spacing: $block-grid-default-spacing,
      $base-style: true
    );
  }

  &__item {
    border: 1px solid $grey;
    border-radius: 5px;
    padding: 5px;
  }

  &__demo {
    height: 50px;
  }
}


.icon-list {
  li {
    margin: 20px;
  }

  .icon {
    margin-right: rem-calc(20);
      vertical-align: middle;
      font-size: rem-calc(30);
  }
}