.squaredImageItem {
  @include display(flex);
  @include flex-direction(row);

  &.squaredImageItem--forcedRow {
    @include flex-direction(row);
  }

  // fix IE10 bugs
  overflow: visible;
  width:100%;

  @media #{$medium-up} {
    @include flex-direction(row);
  }

  &__img {
    position: relative;
    width: 42%;

    // fix IE10 bugs
    @include flex-shrink(1);
    @include flex-basis(auto);
    min-width: 1px;

    img {
      max-width: 100%;
      width: 100%;
    }

    &__tags {
      @include position(absolute, rem-calc(10) rem-calc(10) null null);
      text-align: right;
      @extend .ul-unstyled;

      li {
        margin-bottom: rem-calc(10);
      }
    }
  }

  &__infos {
    position: relative;
    width: 58%;
    padding-left: ($column-gutter * 1.25);

    // fix IE10 bugs
    @include flex-shrink(1);
    @include flex-basis(auto);

    @media #{$medium-up} {
      @include display(flex);
      @include flex-direction(column);
      @include justify-content(space-between);
      padding-left: ($column-gutter * 2);
    }

    .heading {
      padding: 0;
      display: block;
    }
  }

  &__details {
    margin-top: rem-calc(-5);

    &__1,
    &__2 {
      margin: 0;
      line-height: rem-calc(30*2/3);
    }

    &__1 {
      margin-top: 7px;
      font-size: rem-calc(20*2/3);
      font-weight: $font-weight-bold;
      color: $jet;
    }
    &__2 {
      font-size: rem-calc(20*2/3);
      font-weight: $font-weight-normal;
      color: $grey;
      strong {
        font-weight: $font-weight-bold;
        font-size: rem-calc(30*2/3);
      }
    }

    @media #{$medium-up} {
      &__1,
      &__2 {
        font-size: rem-calc(20);
        line-height: rem-calc(30);
      }
    }
  }

  &__btn {
    margin-top: rem-calc(30);
    .btn-primary {
      margin-bottom: 0;
    }
  }

  &--stacked {
    &.squaredImageItem {
      @include flex-direction(column);

      .squaredImageItem__img {
        width: 100%;
      }

      .squaredImageItem__infos {
        width: 100%;
        padding-left: 0;

        .heading {
          padding: rem-calc(30*2/3) 0;
          margin-bottom: 0;

          &__title {
            margin-bottom: 0;
          }
        }
      }
    }
  }

  &--stacked-small {
    @media #{$small-only} {
      .squaredImageItem {
        &__img {
          width: 100%;
        }

        &__infos {
          width: 100%;

          .heading {
            padding: rem-calc(30*2/3) 0;
            margin-bottom: rem-calc(30*2/3);
            border-bottom: 1px solid $mediumGrey;

            &__title {
              margin-bottom: 0;
              //font-size: rem-calc(40*2/3);
            }
          }
        }

        &__details__1,
        &__details__2 {
          line-height: rem-calc(50*2/3);
          font-size: rem-calc(30*2/3);
          strong {
            font-size: rem-calc(40*2/3);
          }
        }

        &__btn {
          .btn-primary {
            width: 100%;
          }
        }
      }
    }
  }
}
