.pageHeaderNav {
  margin-top: rem-calc(-70*2/3);
  position: relative;
  z-index: 1;

  &__list {
    @extend .ul-unstyled;
    @include display(flex);
    @include flex-direction(column);
    width: 100%;

    &__item {
      @include display(flex);
      @include align-items(center);
      @include justify-content(center);
      height: rem-calc(70*2/3);
      border: 1px solid $mediumGrey;
      border-top: none;
      background: $white;

      a {
        font-size: rem-calc(26*2/3);
        color: $jet;
        display: block;
        width: 100%;
        padding: 0 rem-calc(10);
        text-align: center;
      }

      &.active,
      .no-touch &:hover,
      .touch &:active,
      &:focus {
        background: $blue;
        border-color: $blue;
        a {
          font-weight: $font-weight-bold;
          color: $white;
        }
      }
    }
  }
}

@media #{$small-only} {
  .pageHeaderNav {
    padding: 0;
  }
}

@media #{$medium-up} {
  .pageHeaderNav {
    &__list {
      @include flex-direction(row);

      &__item {
        @include flex(1);
        border: 1px solid $mediumGrey;
        border-left: none;

        a {
          font-size: rem-calc(14);
        }
      }
    }
  }
}

