@media #{$medium-only} {
  body {
    overflow-x: hidden;
  }

  @media #{$portrait} {
    html, body {
      font-size: 80%
    }
  }
}

@media #{$small-only} {
  body {
    overflow-x: hidden;
  }
}


// WRAPPERS
// main site wrapper
.main-wrapper {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;

  @media #{$medium-up} {
    overflow: inherit;
    max-width: $xlarge-breakpoint;
    min-width: em-calc(900);
    padding-left: 0;
    padding-right: 0;
  }
}

// large content wrapper
.wrapper {
  width: 100%;
  padding-left: $column-gutter-small;
  padding-right: $column-gutter-small;
  margin: 0 auto;
}
.wrapper--medium-up {
  padding-left: 0;
  padding-right: 0;
  margin: 0 auto;
  width: 100%;
}

@media #{$medium-only} {
  .wrapper,
  .wrapper--medium-up {
    padding-left: ($column-gutter * 3);
    padding-right: ($column-gutter * 3);
  }
}

@media #{$large-up} {
  .wrapper,
  .wrapper--medium-up {
    max-width: em-calc(1220);
    padding-left: $column-gutter;
    padding-right: $column-gutter;
  }
}

// small wrapper to center block
@media #{$small-only} {
  .small-wrapper {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
}


// section spacing
.section-padding {
  padding-top: rem-calc(40*2/3);
  padding-bottom: rem-calc(40*2/3);

  @media #{$medium-up} {
    padding-top: rem-calc(50);
    padding-bottom: rem-calc(50);
  }
}

// prevent padding top on body (really bad)
.f-topbar-fixed {
  padding-top: 0 !important;

  #container {
    padding-top: $topbar-height;
  }
}


// small headers
.narrow-header {
  min-height: rem-calc(400*2/3);
  padding-top: rem-calc(130*2/3);
  background-size: cover;
  background-position: center center;

  .heading {
    padding-bottom: rem-calc(100*2/3);
  }

  @media #{$medium-up} {
    min-height: rem-calc(400);
    padding-top: rem-calc(130);
  }
}


// centered content
.content-centered {
  margin: rem-calc(15*2/3) auto;

  @media #{$medium-up} {
    width: 45%;
    margin: rem-calc(30) auto;
  }
}


// remove lateral paddings on .row elements
.row--noGutter {
  .columns {
    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
    }
  }
}


// swap item trick
@media #{$medium-up} {
  .swapItem {
    width: 100%;
    display: table;

    &__1 {
      display: table-header-group;
    }
    &__2 {
      display: table-row-group;
    }
    &__3 {
      display: table-footer-group;
    }
  }
}
