.testimonials {
  padding-left: 5%;
  padding-right: 5%;

  @media #{$medium-up} {
    padding: 0;
  }

  &--triple {
    @media #{$medium-up} {
      padding: 0;
      @include grid-row(
        $behavior: 'nest-collapse'
      );
    }

    .testimonials__item {
      @media #{$medium-up} {
        @include grid-column(
          $columns: 4,
          $collapse: true
        );
      }
    }
  }

  &__img {
    width: 100%;
  }

  &__item {
    position: relative;
    text-align: center;
  }

  &__quote {
    @include position(absolute, 50% null null 50%);
    width: 100%;
    max-width: 460px;
    @include transform(translateY(-50%) translateX(-50%));
    padding: 0 5%;
    border: none;
    margin: 0;
    p {
      color: $white;
      font-size: rem-calc(30);
      line-height: 120%;

      @media #{$medium-up} {
        font-size: rem-calc(25);
      }
    }
  }

  &__cite {
    position: absolute;
    bottom: 2%; left: 0; right: 0;
    color: $white;
    font-size: rem-calc(20);
    margin-bottom: 0;

    &:before {
      content: "";
      @include position(absolute, -15px 50% null null);
      width: 40px;
      height: 5px;
      background: $white;
      @include transform(translateX(50%));
    }

    @media #{$medium-up} {
      font-size: rem-calc(15);
    }
  }
}